.sp-archive {
  overflow-x: clip;
}

.sp-block__meta {
  position: relative;
}

.sp-block__mediaLink,
.sp-block__metaLink {
  position: absolute;
  inset: 0;
  z-index: 9;
  text-indent: -9999px;
  overflow: hidden;
  color: inherit;
  text-decoration: none;
  background: transparent;
}

.sp-stream {
  display: flex;
  flex-direction: column;
  row-gap: 0;
  margin-top: 96px;
}

.sp-block {
  position: relative;
  z-index: var(--z, 1);
  display: flex;
  flex-direction: column;
  background-color: transparent;
}

.sp-block__media {
  position: relative;
  align-self: flex-start;
  background-color: $bg;
}

.sp-block.is-even .sp-block__media {
  align-self: flex-end;
}

.sp-block__mediaWrap {
  position: relative;
  z-index: 2;
  display: flex;
  justify-content: flex-end;
  padding-left: $pad-extra;
  box-sizing: content-box;
}

.sp-block__mediaWrap img {
  display: block;
  height: 370px;
  width: auto;
}

.sp-block__mediaTwin {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  height: 370px;
  width: calc(var(--media-w, 946px) + #{$pad-extra} - 45px);
  background-color: $bg;
  pointer-events: none;
  border-top: 0;
  border-right: 0;
  border-left: 0;
  border-bottom: 0;
}

.sp-block__meta {
  height: 98px;
  width: 150%;
  box-sizing: border-box;
  display: flex;
  border-bottom: 1px solid #000;
}

.sp-block.is-odd {
  transform: translateX(-45px);
}

.sp-block.is-even .sp-block__meta {
  border-bottom: 0;
}

.sp-block.is-even .sp-block__meta {
  width: 100%;
}

.sp-block__metaInner {
  position: relative;
  z-index: 5;
  width: calc(var(--media-w, 946px) + #{$pad-extra});
  padding-inline: 10px;
  column-gap: 16px;
  padding-top: 25px;
}

.sp-block.is-even .sp-block__metaInner {
  margin-left: auto;
  border-bottom: 1px solid #000;
  position: relative;
}

.sp-block.is-even .sp-block__metaInner::after {
  content: "";
  position: absolute;
  left: 100%;
  bottom: -1px;
  height: 1px;
  width: 100vw;
  background: #000;
}

.sp-block__textSkew {
  display: flex;
  align-items: flex-end;
  gap: 16px;
  transform: skewX(-30deg);
  transform-origin: left center;
  height: 72px;
  padding-block: 15px;
}

.sp-block__title {
  font-size: 42px;
  line-height: 1.2;
}

.sp-block__subtitles {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
}

.sp-block__subtitle {
  font-size: 26px;
  line-height: 1.3;
}

.sp-stream > .sp-block:nth-child(2) {
  transform: translateY(-45px);
  margin-right: calc(140px + 116px);
}

.sp-stream > .sp-block:nth-child(3) {
  margin-left: 256px;
}

.sp-stream > .sp-block:nth-child(4) {
  transform: translateY(-45px);
  margin-right: 140px;
}

.sp-block__media:hover ~ .sp-block__meta {
  border-bottom: 0;
}

.sp-block__media:hover ~ .sp-block__meta .sp-block__metaInner {
  padding-inline: 0;
  background-color: rgba(215, 219, 222, 1);
  width: calc(var(--media-w, 946px));
}

.sp-block__media:hover ~ .sp-block__meta .sp-block__metaInner::after {
  display: none;
}

.sp-block__media:hover ~ .sp-block__meta .sp-block__metaInner::before {
  content: "";
  position: absolute;
  z-index: 6;
  pointer-events: none;
  left: -3px;
  right: -3px;
  bottom: -3px;
  top: calc(-370px - 3px);
  border: 1px solid #000;
}

.sp-block.is-odd .sp-block__media:hover ~ .sp-block__meta .sp-block__metaInner {
  transform: translateX(45px);
}

.sp-block__media:hover ~ .sp-block__meta .sp-block__textSkew {
  transform: none;
}

.sp-block__media:hover .sp-block__mediaTwin {
  transform: translate(72px, -24px);
  border-top: 1px solid #000;
  border-right: 1px solid #000;
  border-left: 0;
  border-bottom: 0;
}

.sp-block__media:hover .sp-block__mediaTwin::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 100%;
  height: 1px;
  width: 100vw;
  background: #000;
  pointer-events: none;
}

.sp-block__media:hover ~ .sp-block__meta .sp-block__metaInner {
  border-bottom: 0 !important;
}
